import React from 'react'
import * as VKConnect from '@vkontakte/vkui-connect'
import {View} from '@vkontakte/vkui'
import '@vkontakte/vkui/dist/vkui.css'

import Home from './panels/Home'
import Persik from './panels/Persik'

class App extends React.Component {
    constructor(props) {
        super(props)

        this.state = {
            activePanel: 'home',
            fetchedUser: {}
        }
    }

    componentWillMount() {
        // VK Connect
        VKConnect.subscribe((e) => {
            if (!e.detail) {
            } else if (e.detail.type === 'VKWebAppGetUserInfoResult') {
                this.setState({
                    fetchedUser: {
                        ...e.detail.data
                    }
                })
            }
        })
        // It is necessary to call VKWebAppInit to launch the app
        VKConnect.send('VKWebAppInit', {})

        VKConnect.send('VKWebAppGetUserInfo', {})
    }

    render() {
        return (
            <View activePanel={this.state.activePanel} header={true}>
                <Home
                    id="home"
                    fetchedUser={this.state.fetchedUser}
                    clickHandler={() => this.setState({activePanel: 'persik'})}
                />
                <Persik
                    id="persik"
                    clickHandler={() => this.setState({activePanel: 'home'})}
                />
            </View>
        )
    }
}

export default App



